<template>
  <div class="login-wrap">

  </div>
</template>

<script type="text/ecmascript-6">
  import Vue from 'vue'
  import axios from 'axios'
  import qs from 'qs'
  import {Utils} from 'common/js/Utils'
  import {ERR_OK} from 'common/js/Utils'

  export default {
    data () {
      return {
        ruleform:{
          uname:'',
          psd:''
        }
      }
    },
    created(){
      this.getUrlInfo()
    },
    methods: {
      getUrlInfo(){
        if((/\?username/).test(window.location.href)){
          let urlInfo = window.location.href.split('?')[1]
          let infoList = urlInfo.split('&')
          let info=[]
          for(let i = 0;i < infoList.length; i++){
            info[i] = infoList[i].split('=')[1]
          }
          this.ruleform.uname = info[0]
          this.ruleform.psd = info[1]
          let params = {
            username:info[0],
            password:info[1]
          }
          this.axiosPost(params)
        }
      },
      axiosPost(params){
        this.axios.post(Utils.login,qs.stringify(params)).then((response) => {
//          console.log(response)
          if(response.data.code === ERR_OK){
            this.$message({
              duration:1500,
              message: '登录成功',
              type:'success'
            });
            window.localStorage.setItem('nGGname',this.ruleform.uname)
            window.localStorage.setItem('nGGuser_id',response.data.data.user_id)
            window.localStorage.setItem('nGGtoken',response.data.data.token)
            this.$router.push({path:'/index'})
          }else if(response.data.code === 40003 || response.data.code === 40004){
            this.$message({
              duration:1500,
              message:'账号不存在或被锁定',
              type:'error'
            });
          }else{
            this.$message({
              duration:1500,
              message:'账号或密码输入有误',
              type:'error'
            })
          }
        })
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">

</style>



